$paddingColour: #312f33;

.root {
  display: flex;
  height: 100vh;
  flex-direction: column;
  background-color: #222024;
}

.top {
  height: 15vh;
  background-color: $paddingColour;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

.main {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.bottom {
  height: 15vh;
  background-color: $paddingColour;
  box-shadow: rgba(0, 0, 0, 0.24) 0px -3px 8px;
}

.content {
  color: white;
  font-size: 30px;
  padding: 4px;
  text-align: center;
  font-family: "Gill Sans", sans-serif;
}
